.template-swiper{margin-top:75px}.template-swiper .title a{margin-left:10px;font-size:1.1rem;text-decoration:none;color:#e21221}.template-swiper .swiper{padding-top:46px;position:relative;width:100%;min-height:400px}.template-swiper .swiper-wrapper{width:90%}.template-swiper .swiper-button-next,.template-swiper .swiper-button-prev{position:absolute;top:0px;z-index:10;right:0}.template-swiper .swiper-button-next:before,.template-swiper .swiper-button-prev:before{content:"➧";color:#fff;opacity:.85;font-size:2.85rem}.template-swiper .swiper-button-next:hover::before,.template-swiper .swiper-button-prev:hover::before{transition:all .3s ease-out;color:#e21221}.template-swiper .swiper-button-prev{right:30px;transform:rotateY(180deg)}.template-swiper .swiper-button-disabled{opacity:.5}.template-swiper .swiper-button-disabled:hover::before{color:#fff}